import React from "react";
import ReactDOM from "react-dom";

function Setabout(){
    return <>
    <h1>About</h1>
    <p>I created this website to help out fellow players(and also to test my skills in React JS).Hope you'll find all the info here useful.The setups will be updated whenever I make changes to my team or if I find new setups from others.</p>
    </>

}

export function About(){
    ReactDOM.render(
        <Setabout/>,
        document.getElementById("main")
    )
}